import React, { createContext, useState, useContext } from "react";
import { BrowserRouter as Router, Route, Switch, Redirect } from "react-router-dom";
import LoginPage from "components/LoginPage/LoginPage";
// core components
import Admin from "layouts/Admin.js";
import PrivateRoute from "components/PrivateRoute/PrivateRoute";
import Error404 from "components/Error404/Error404.js";
//Context
import { ErrorStatusContext, UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider"

function App() {

  const userPrivilege = useContext(UserPrivilegeContext);
  const errorStatus = useContext(ErrorStatusContext);

  return (
        <Router>
          <Switch>
            {!errorStatus && <>
            <Route path="/login" component={LoginPage} />
            <PrivateRoute
              isLoggedIn={userPrivilege >= 0 ? true : false}
              path="/admin"
              component={Admin}
            />
            <Redirect from="/" to="/login" />
            </>
            }
            {errorStatus && <>
            <Route path="/error" component={Error404} />
            <Redirect from="/" to="/error" />
            </>
            }
          </Switch>
        </Router>
  );
}

export default App;
