import React, { useState, useEffect, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
//Context
import { UserNameContext, UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider"
import { isEmpty } from "validator";
//RBAC
import Can from "components/Can/Can"
//General variables
import { statusValues } from "variables/general"

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    marginTop: "50px",
    align: "left",
    borderRadius: "1.5em",
    boxShadow: "0px 11px 35px 2px rgba(0, 0, 0, 0.14)",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  button: {
    margin: "10px",
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function RepresentativeForm(props) {
  const { onEdit, onDelete, editedRepresentative } = props;

  const classes = useStyles();

  //Name of current user to be used in the logs
  const userName = useContext(UserNameContext);
  //Privilege level of the current user
  const userPrivilege = useContext(UserPrivilegeContext);

  const [representative, setRepresentative] = useState({
    id: 0,
    fname: "",
    lname: "",
    contact: "",
    note: "",
    status: 1,
  });
  const [edit, setEdit] = useState(false);
  const [representativeError, setRepresentativeError] = useState({
    fname: false,
    lname: false,
    contact: false,
    note: false,
    status: false,
  });
  
  //This one is to define whether some buttons are disabled or not based on privileges
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);

  useEffect(() => {
    setRepresentative(editedRepresentative);
  }, [editedRepresentative]);

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "fname") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        fname: value,
        }
      });
    }

    if (name === "lname") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        lname: value,
        }
      });
    }

    if (name === "contact") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        contact: value,
        }
      });
    }

    if (name === "note") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        note: value,
        }
      });
    }

    if (name === "status") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        status: value,
        }
      });
    }

    validateField(!isEmpty(value.toString()), name);
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setRepresentativeError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setRepresentativeError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(representative.fname), "fname");
    const check2 = validateField(!isEmpty(representative.lname), "lname");
    return check1 && check2;
  }

  function resetFields() {
    setRepresentative({
      id: 0,
      fname: "",
      lname: "",
      contact: "",
      note: "",
      status: 1,
    });
    setRepresentativeError({
      fname: false,
      lname: false,
      contact: false,
      note: false,
      status: false,
    });
  }

  const handleSave = () => {
    if (!validateAll()) {
      return;
    }
    onEdit(representative);
    setEdit(false);
    resetFields();
  };

  const handleEdit = () => {
    setEdit(true);
  };

  const handleDelete = () => {
    onDelete(representative);
    resetFields();
  };

  const handleCancelEdit = () => {
    setEdit(false);
    resetFields();
    setRepresentative(editedRepresentative);
  };

  return (
    <div className={classes.mainDiv}>
      <Can 
          role={userPrivilege} 
          perform="representatives:edit"
          data={[]} 
          yes={() => {
            setIsButtonDisabled(false);
            return null
          }}
          no={() => {
            setIsButtonDisabled(true);
            return null
          }}
      />
      <DialogTitle id="form-dialog-title">
        Informacin de Vendedor
      </DialogTitle>
      <DialogContent>
        {!edit && (
            <div>
              <TextField
                disabled
                value={representative.fname}
                name="fname"
                label="Nombre"
                style={{ margin: 8 }}
                placeholder=""
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                disabled
                value={representative.lname}
                name="lname"
                label="Apellido"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                    representativeError.lname && "Este campo no puede estar vacio"
                  }
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                disabled
                multiline
                value={representative.contact}
                name="contact"
                label="Email"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                disabled
                multiline
                value={representative.note}
                name="note"
                label="Nota"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                disabled
                value={statusValues[representative.status || 0].label}
                name="status"
                label="Estado"
                className={classes.textField}
                helperText=""
                margin="normal"
                InputLabelProps={{
                    shrink: true,
                  }}
              />
            </div>
        )}

        {edit && (
         <div>
         <TextField
           error={representativeError.fname}
           onChange={handleChange}
           value={representative.fname}
           name="fname"
           label="Nombre"
           style={{ margin: 8 }}
           placeholder=""
           helperText={
             representativeError.fname && "Este campo no puede estar vacio"
           }
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           error={representativeError.lname}
           onChange={handleChange}
           value={representative.lname}
           name="lname"
           label="Apellido"
           style={{ margin: 8 }}
           placeholder=""
           helperText={
               representativeError.lname && "Este campo no puede estar vacio"
             }
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           multiline
           onChange={handleChange}
           value={representative.contact}
           name="contact"
           label="Email"
           style={{ margin: 8 }}
           placeholder=""
           helperText=""
           fullWidth
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           multiline
           onChange={handleChange}
           value={representative.note}
           name="note"
           label="Nota"
           style={{ margin: 8 }}
           placeholder=""
           helperText=""
           fullWidth
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           select
           error={representativeError.status}
           onChange={handleChange}
           value={representative.status}
           name="status"
           label="Estado"
           className={classes.textField}
           helperText=""
           margin="normal"
         >
           {statusValues.map((option) => (
             <MenuItem key={option.value} value={option.value}>
               {option.label}
             </MenuItem>
           ))}
         </TextField>
       </div>
        )}
      </DialogContent>
      <DialogActions>
        {!edit && (representative.id > 0 ?
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        :
          <>
            <Button
              disabled
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        )}
        {edit && (
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleCancelEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Cancelar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleSave}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Guardar
            </Button>
          </>
        )}
      </DialogActions>
    </div>
  );
}
