import { baseUrl } from "variables/general.js"

const getAllRepresentatives = (setTableData, onError, token) => {
  const queryUrl = baseUrl + "Representatives";

  fetch(queryUrl, {
    method: "GET",
    headers: {
      "Authorization": token,
    },
  })
    .then((res) => res.json())
    .then((fetchedData) => {
      setTableData(fetchedData);
    })
    .catch((err) => {
      onError(err);
    });
};

const createRepresentative = (body, onSuccess, onError, token) => {
  const queryUrl = baseUrl + "Representatives";
  // creates entity
  fetch(queryUrl, {
    method: "POST",
    headers: {
      "content-type": "application/json",
      accept: "application/json",
      "Authorization": token,
    },
    body: JSON.stringify(body),
  })
  .then((res) => {
    if (!res.ok) {
      throw Error(res.status);
    }
    return res;
  })
    .then((response) => {
      onSuccess(response.status);
    })
    .catch((err) => {
      onError(err);
    });
};

const updateRepresentative = (body, onSuccess, onError, token) => {
  // this will update entries with PUT
  const queryUrl = baseUrl + "Representatives";
  fetch(queryUrl, {
    method: "PUT",
    headers: {
      "content-type": "application/json",
      accept: "application/json",
      "Authorization": token,
    },
    body: JSON.stringify(body),
  })
  .then((res) => {
    if (!res.ok) {
      throw Error(res.status);
    }
    return res;
  })
    .then((response) => {
      onSuccess(response.status);
    })
    .catch((err) => {
      onError(err);
    });
};

const removeRepresentative = (representativeId, onSuccess, onError, token) => {
  const idParam = { id: representativeId };
  var queryUrl = new URL(baseUrl + "Representatives");
  queryUrl.search = new URLSearchParams(idParam).toString();
  // deletes entities
  fetch(queryUrl, {
    method: "DELETE",
    headers: {
      "Authorization": token,
    },
  })
  .then((res) => {
    if (!res.ok) {
      throw Error(res.status);
    }
    return res;
  })
  .then((response) => {
    onSuccess(response.status);
  })
  .catch((err) => {
    onError(err);
  });
};

export { getAllRepresentatives, createRepresentative, updateRepresentative, removeRepresentative };
