import React, { useState, useEffect, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import { isEmpty } from "validator";
//Context
import { UserNameContext, UserEmailContext, UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider"
//RBAC
import Can from "components/Can/Can"

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    marginTop: "50px",
    align: "left",
    borderRadius: "1.5em",
    boxShadow: "0px 11px 35px 2px rgba(0, 0, 0, 0.14)",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  button: {
    margin: "10px",
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function ClientForm(props) {
  const { onEdit, onAddLog, onDelete, editedClient, representatives } = props;
  //Original status of the client. Used to define whether to log a status change
  const originalStatus = Number(editedClient.status);

  const classes = useStyles();
  //Name of current user to be used in the logs
  const userName = useContext(UserNameContext);
  // Email of current user
  const userEmail = useContext(UserEmailContext);
  //Privilege level of the current user
  const userPrivilege = useContext(UserPrivilegeContext);
  const [client, setClient] = useState({
    client_name: "",
    client_code: "",
    owner_representative_id: 0,
    owner_representative_name: "",
    capturing_representative_id: 0,
    capturing_representative_name: "",
    note: "",
    contact: "",
    products: "",
    type: 1,
    status: 1,
  });
  const [edit, setEdit] = useState(false);
  //Did the status of the edited client change? This one is used for the client log
  const [didStatusChange, setDidStatusChange] = useState(false);
  //This one is used to open the client log modal. Here you must give a reason as to why you changed the client status
  const [openAppLog, setOpenAppLog] = useState(false);
  //This one is to define whether some buttons are disabled or not based on privileges
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  //Email of activity's owner representative
  const [representativeEmail, setRepresentativeEmail] = useState("");

  //Error flags for every field in the Client object. The logDescription field is for the AppLog object. 
  const [clientError, setClientError] = useState({
    client_name: false,
    client_code: false,
    owner_representative_name: false,
    capturing_representative_name: false,
    contact: false,
    note: false,
    type: false,
    products: false,
    status: false,
    logDescription: false,
  });

  //Log object for whenever a client's state is changed 
  const [appLog, setAppLog] = useState({
    log_type: "Cambio de estado en cliente",
    log_description: "",
    log_date: new Date(),
  });

  useEffect(() => {
    if(editedClient.client_code) {
      setClient(editedClient);
      const owner_representative = representatives.filter(
      (representative) => Number(representative.id) === Number(editedClient.owner_representative_id))[0];
      setRepresentativeEmail(owner_representative.contact);
    }
  }, [editedClient]);

  const statusValues = [
    {
      value: 0,
      label: "Inactivo",
    },
    {
      value: 1,
      label: "Activo",
    },
  ];

  const typeValues = [
    {
      value: 1,
      label: "Ideal",
    },
    {
      value: 0,
      label: "No ideal",
    },
  ];

  function handleChange(event) {

    const { value, name } = event.target;

    if (name === "logDescription") {
      //Create a custom log message with all relevant information
      let new_status = Number(editedClient.status) === 1 ? "Inactivo" : "Activo"
      let descriptionText = 
        "El usuario " + userName + " actualiz el estado del cliente "
        + editedClient.client_name + " con RIF " + editedClient.client_code +
        " a " + new_status + ". " + 
        "Razn del cambio: " + value;
      setAppLog((prevValue) => {
        return {
        ...prevValue,
        log_description: descriptionText
        }
      })
    }

    if (name === "client_name") {
      setClient((prevState) => {
        return {
        ...prevState,
        client_name: value,
      };
    });
    }

    if (name === "client_code") {
      setClient((prevState) => {
      return {
        ...prevState,
        client_code: value,
      };
    });
    }

    if (name === "owner_representative_name") {
      const filteredRepresentative = representatives.filter(
        (representative) =>
          representative.fname + " " + representative.lname === value
      );
      setClient((prevState) => {
      return {
        ...prevState,
        owner_representative_name: value,
        owner_representative_id: filteredRepresentative[0].id,
      }
      });
    }

    if (name === "capturing_representative_name") {
      const filteredRepresentative = representatives.filter(
        (representative) =>
          representative.fname + " " + representative.lname === value
      );
      setClient((prevState) => {
      return {
        ...prevState,
        capturing_representative_name: value,
        capturing_representative_id: filteredRepresentative[0].id,
      }
      });
    }

    if (name === "contact") {
      setClient((prevState) => {
      return {
        ...prevState,
        contact: value,
      };
    });
    }

    if (name === "note") {
      setClient((prevState) => {
      return {
        ...prevState,
        note: value,
      };
    });
    }

    if (name === "products") {
      setClient((prevState) => {
      return {
        ...prevState,
        products: value,
      };
    });
    }

    if (name === "type") {
      setClient((prevState) => {
      return {
        ...prevState,
        type: value,
      };
    });
    }

    if (name === "status") {
      if (value !== originalStatus) {
        setDidStatusChange(true);
      } else {
        setDidStatusChange(false);
      }
      setClient((prevState) => {
      return {
        ...prevState,
        status: value,
      };
    });
    }
    validateField(!isEmpty(value.toString()), name);
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setClientError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setClientError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(client.client_name), "name");
    const check2 = validateField(!isEmpty(client.client_code), "code");
    const check3 = !didStatusChange || validateField(!isEmpty(appLog.log_description), "logDescription");

    return check1 && check2 && check3;
  }

  function resetFields() {
    setClient({
      client_name: "",
      client_code: "",
      owner_representative_id: 0,
      owner_representative_name: "",
      capturing_representative_id: 0,
      capturing_representative_name: "",
      note: "",
      contact: "",
      products: "",
      type: 1,
      status: 1,
    });
    setClientError({
      client_name: false,
      client_code: false,
      owner_representative_id: false,
      owner_representative_name: false,
      capturing_representative_id: false,
      capturing_representative_name: false,
      note: false,
      contact: false,
      products: false,
      type: false,
      status: false,
    });
    setAppLog({
      log_type: "Cambio de estado en cliente",
      log_description: "",
      log_date: new Date(),
    });
  }

  const handleSave = () => {
    if (!validateAll()) {
      return;
    }
    console.log(client);
    onEdit(client);
    setEdit(false);
    //Save log if status changed.
    if (didStatusChange) {
      console.log(appLog);
      onAddLog(appLog)
    }
    setDidStatusChange(false);
    resetFields();
  };

  const handleEdit = () => {
    setEdit(true);
  };

  const handleDelete = () => {
    onDelete(client);
    resetFields();
  };

  const handleCancelEdit = () => {
    setEdit(false);
    resetFields();
    setClient(editedClient);
  };

  return (
    <div className={classes.mainDiv}>
      <Can 
          role={userPrivilege} 
          perform="clients:edit"
          data={{
            userEmail: userEmail, 
            clientOwnerEmail: representativeEmail,
          }} 
          yes={() => {
            setIsButtonDisabled(false);
            return null
          }}
          no={() => {
            setIsButtonDisabled(true);
            return null
          }}
      />
      <DialogTitle id="form-dialog-title">Informacin de Cliente</DialogTitle>
      <DialogContent>
        {!edit && (
          <div>
            <TextField
              disabled
              multiline
              value={client.client_name}
              name="client_name"
              label="Nombre"
              style={{ margin: 8 }}
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              multiline
              value={client.client_code}
              name="client_code"
              label="RIF"
              style={{ margin: 8 }}
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              select
              error={clientError.owner_representative_name}
              onChange={handleChange}
              value={client.owner_representative_name}
              name="owner_representative_name"
              label="Vendedor asignado"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                clientError.owner_representative_name &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {representatives.map((representative) => (
                <MenuItem
                  key={representative.id}
                  value={representative.fname + " " + representative.lname}
                >
                  {representative.fname + " " + representative.lname}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              disabled
              select
              error={clientError.capturing_representative_name}
              onChange={handleChange}
              value={client.capturing_representative_name}
              name="capturing_representative_name"
              label="Capturado por"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                clientError.capturing_representative_name &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {representatives.map((representative) => (
                <MenuItem
                  key={representative.id}
                  value={representative.fname + " " + representative.lname}
                >
                  {representative.fname + " " + representative.lname}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              disabled
              multiline
              value={client.contact  || ""}
              name="contact"
              label="Informacin de contacto"
              style={{ margin: 8 }}
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              multiline
              value={client.note || ""}
              name="note"
              label="Nota"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              multiline
              value={client.products || ""}
              name="products"
              label="Productos de inters"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              value={Number(client.status) === 1 ? "Activo" : "Inactivo"}
              name="status"
              label="Estado"
              className={classes.textField}
              helperText=""
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              value={
                Number(client.type) === 1
                  ? "Ideal" : "No ideal"
              }
              name="type"
              label="Tipo"
              className={classes.textField}
              helperText=""
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
          </div>
        )}

        {edit && (
          <div>
            <TextField
              onChange={handleChange}
              value={client.client_name}
              name="client_name"
              label="Nombre"
              style={{ margin: 8 }}
              placeholder=""
              helperText={clientError.client_name && "Este campo no puede estar vacio"}
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              onChange={handleChange}
              value={client.client_code}
              name="client_code"
              label="RIF"
              style={{ margin: 8 }}
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              select
              error={clientError.owner_representative_name}
              onChange={handleChange}
              value={client.owner_representative_name}
              name="owner_representative_name"
              label="Vendedor asignado"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                clientError.owner_representative_name &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {representatives.map((representative) => (
                <MenuItem
                  key={representative.id}
                  value={representative.fname + " " + representative.lname}
                >
                  {representative.fname + " " + representative.lname}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              select
              error={clientError.capturing_representative_name}
              onChange={handleChange}
              value={client.capturing_representative_name}
              name="capturing_representative_name"
              label="Capturado por"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                clientError.capturing_representative_name &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {representatives.map((representative) => (
                <MenuItem
                  key={representative.id}
                  value={representative.fname + " " + representative.lname}
                >
                  {representative.fname + " " + representative.lname}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              multiline
              onChange={handleChange}
              value={client.contact || ""}
              name="contact"
              label="Informacin de contacto"
              style={{ margin: 8 }}
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              multiline
              onChange={handleChange}
              value={client.note || ""}
              name="note"
              label="Nota"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              multiline
              onChange={handleChange}
              value={client.products || ""}
              name="products"
              label="Productos de inters"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              select
              onChange={handleChange}
              value={client.status || 0}
              name="status"
              label="Estado"
              className={classes.textField}
              helperText=""
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {statusValues.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              select
              onChange={handleChange}
              value={client.type}
              name="type"
              label="Tipo"
              className={classes.textField}
              helperText=""
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {typeValues.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            {didStatusChange && 
            <TextField
              multiline
              rows={2}
              onChange={handleChange}
              value={appLog.description}
              name="logDescription"
              label="Razn cambio de estado?"
              style={{ margin: 8 }}
              placeholder=""
              helperText={clientError.logDescription && "Este campo no puede estar vacio"}
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />}
          </div>
        )}
      </DialogContent>
      <DialogActions>
        {!edit && client.client_code && (
          <>
            <Button disabled={isButtonDisabled} onClick={handleDelete} color="secondary" variant="contained" className={classes.button}>
              Borrar
            </Button>
            <Button disabled={isButtonDisabled} onClick={handleEdit} color="primary" variant="contained" className={classes.button}>
              Editar
            </Button>
          </>
        )}
        {!edit && !client.client_code && (
          <>
            <Button disabled onClick={handleDelete} color="secondary" variant="contained" className={classes.button}>
              Borrar
            </Button>
            <Button disabled onClick={handleEdit} color="primary" variant="contained" className={classes.button}>
              Editar
            </Button>
          </>
        )}
        {edit && (
          <>
            <Button
              onClick={handleCancelEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Cancelar
            </Button>
            <Button onClick={handleSave} color="primary" variant="contained" className={classes.button}>
              Guardar
            </Button>
          </>
        )}
      </DialogActions>
    </div>
  );
}

