import React, { useState, useEffect, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import MenuItem from "@material-ui/core/MenuItem";
import { isEmpty, isDate } from "validator";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import spaLocale from "date-fns/locale/es";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from "@material-ui/pickers";
//Context
import { UserNameContext, UserEmailContext, UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider"
//RBAC
import Can from "components/Can/Can"

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    marginTop: "50px",
    align: "left",
    borderRadius: "1.5em",
    boxShadow: "0px 11px 35px 2px rgba(0, 0, 0, 0.14)",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  button: {
    margin: "10px",
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function ActivityForm(props) {
  const { onEdit, onDelete, clients, representatives, editedActivity } = props;

  const classes = useStyles();

  //Name of current user
  const userName = useContext(UserNameContext);
  //Privilege level of the current user
  const userPrivilege = useContext(UserPrivilegeContext);
  //Email of current user
  const userEmail = useContext(UserEmailContext);
  const [activity, setActivity] = useState({
    id: 0,
    name: "",
    client_code: "",
    current_status: "",
    description: "",
    representative_name: "",
    action_date: new Date(),
    executed: 0,
  });
  const [activityError, setActivityError] = useState({
    name: false,
    client_code: false,
    current_status: false,
    description: false,
    representative_name: false,
    action_date: false,
    executed: false,
  });
  const [edit, setEdit] = useState(false);

  //These two are for the Autocomplete component
  const [clientAutocompleteValue, setClientAutocompleteValue] = useState({});
  const [clientAutocompleteInput, setClientAutocompleteInput] = useState("");
  //This one is to define whether some buttons are disabled or not based on privileges
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  //Email of activity's owner representative
  const [representativeEmail, setRepresentativeEmail] = useState("");

  useEffect(() => {
    if(editedActivity.id) {
      setActivity(editedActivity);
      const editedActivityClient = clients.filter(
        (client) => client.client_code === editedActivity.client_code)[0];
      setClientAutocompleteValue(editedActivityClient);
      const editedActivityRepresentative = representatives.filter(
        (representative) => Number(representative.id) === Number(editedActivityClient.owner_representative_id))[0];
      setRepresentativeEmail(editedActivityRepresentative.contact);
    }   
  }, [editedActivity]);

  const executedValues = [
    {
      value: 0,
      label: "Por ejecutar",
    },
    {
      value: 1,
      label: "Ejecutado",
    },
  ];

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "current_status") {
      setActivity((prevState) => {
        return {
        ...prevState,
        current_status: value,
      }
      });
    }

    if (name === "description") {
      setActivity((prevState) => {
      return {
        ...prevState,
        description: value,
      }
      });
    }

    // if (name === "owner_representative_name") {
    //   const filteredRepresentative = representatives.filter(
    //     (representative) =>
    //       representative.fname + " " + representative.lname === value
    //   );
    //   setActivity((prevState) => {
    //   return {
    //     ...prevState,
    //     owner_representative_name: value,
    //     owner_representative_id: filteredRepresentative[0].id,
    //   }
    //   });
    // }

    if (name === "action_date") {
      setActivity((prevState) => {
      return {
        ...prevState,
        action_date: value,
      }
      });
    }

    if (name === "executed") {
      setActivity((prevState) => {
        return {
        ...prevState,
        executed: value,
        }
      });
    }

    //validate relevant fields to make sure they are not empty
    if (name === "action_date") {
      validateField(isDate(value, "DD/MM/YYYY"), name);
    } else if (name === "name") {
      if(value.name) {
        validateField(!isEmpty(value.name.toString()), name);
      } 
    } else if (name === "description" || name === "current_status" || name === "representative_name") {
      validateField(!isEmpty(value.toString()), name);
    }
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setActivityError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setActivityError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(activity.name), "name");
    const check2 = validateField(!isEmpty(activity.description), "description");
    const check3 = validateField(
      !isEmpty(activity.current_status),
      "current_status"
    );
    const check4 = validateField(
      !isEmpty(activity.representative_name),
      "representative_name"
    );
    return check1 && check2 && check3 && check4;
  }

  function resetFields() {
    setActivity({
      id: 0,
      name: "",
      client_code: "",
      current_status: "",
      description: "",
      representative_name: "",
      representative_id: "",
      action_date: new Date(),
      executed: 0,
    });
    setActivityError({
      name: false,
      client_code: false,
      current_status: false,
      description: false,
      representative_name: false,
      action_date: false,
      executed: false,
    });
    setClientAutocompleteValue({});
    setClientAutocompleteInput("");
  }

  const handleSave = () => {
    if (!validateAll()) {
      return;
    }
    //Here we save the edited activity
    onEdit(activity);
    //Here we reset all values of the activity form
    setEdit(false);
    resetFields();
  };

  const handleEdit = () => {
    setEdit(true);
  };

  const handleDelete = () => {
    onDelete(activity);
    resetFields();
  };

  const handleCancelEdit = () => {
    setEdit(false);
    resetFields();
    setActivity(editedActivity);
    const editedActivityClient = clients.filter(
      (client) => client.client_code === editedActivity.client_code)[0];
    setClientAutocompleteValue(editedActivityClient);
  };

  return (
    <div className={classes.mainDiv}>
      <Can 
          role={userPrivilege} 
          perform="activities:edit"
          data={{
            userEmail: userEmail, 
            clientOwnerEmail: representativeEmail,
          }} 
          yes={() => {
            setIsButtonDisabled(false);
            return null
          }}
          no={() => {
            setIsButtonDisabled(true);
            return null
          }}
      />
      <DialogTitle id="form-dialog-title">Informacin de Actividad</DialogTitle>
      <DialogContent>
        {!edit && (
          <div>
            <TextField
              disabled
              onChange={handleChange}
              value={activity.name || ""}
              name="name"
              label="Cliente"
              style={{ margin: 8 }}
              placeholder=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              onChange={handleChange}
              value={activity.client_code}
              name="client_code"
              label="RIF"
              style={{ margin: 8 }}
              placeholder=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            ></TextField>
            <TextField
              disabled
              multiline
              onChange={handleChange}
              value={activity.current_status}
              name="current_status"
              label="Estado actual"
              style={{ margin: 8 }}
              placeholder=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              multiline
              onChange={handleChange}
              value={activity.description}
              name="description"
              label="Prximo paso"
              style={{ margin: 8 }}
              placeholder=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              onChange={handleChange}
              value={activity.representative_name}
              name="representative_name"
              label="Vendedor"
              style={{ margin: 8 }}
              placeholder=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={spaLocale}>
              <KeyboardDatePicker
                disabled
                clearable
                style={{ margin: 16, marginLeft: 8 }}
                label="Fecha de actividad"
                value={activity.action_date}
                format="dd/MM/yyyy"
              />
            </MuiPickersUtilsProvider>
            <TextField
              disabled
              onChange={handleChange}
              value={
                parseInt(activity.executed) === 1 ? "Ejecutado" : "Por ejecutar"
              }
              name="executed"
              label="Estado"
              className={classes.textField}
              helperText=""
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
          </div>
        )}

        {edit && (
          <div>
            <Autocomplete
              value={clientAutocompleteValue || ""}
              onChange={(event, newClient) => {
                setClientAutocompleteValue(newClient);
                if(newClient) {
                  setActivity((prevState) => {
                  return {
                    ...prevState,
                    client_code: newClient.client_code,
                    name: newClient.client_name,
                    representative_name: newClient.owner_representative_name,
                  };
                  })
                }
              }}
              inputValue={clientAutocompleteInput}
              onInputChange={(event, input) => {
                setClientAutocompleteInput(input);
              }}
              options={clients}
              getOptionLabel={(client) => client.client_name || ""}
              name="name"
              label="Cliente"
              placeholder=""
              renderInput={(params) => (
                <TextField {...params} label="Cliente" margin="normal" style={{ margin: 8 }} InputLabelProps={{ shrink: true }}/>
              )}
            />
            <TextField
              disabled
              onChange={handleChange}
              value={activity.client_code}
              name="client_code"
              label="RIF"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            ></TextField>
            <TextField
              multiline
              error={activityError.current_status}
              onChange={handleChange}
              value={activity.current_status}
              name="current_status"
              label="Estado actual"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                activityError.current_status &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              multiline
              error={activityError.description}
              onChange={handleChange}
              value={activity.description}
              name="description"
              label="Prximo paso"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                activityError.description && "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              error={activityError.representative_name}
              onChange={handleChange}
              value={activity.representative_name}
              name="representative_name"
              label="Vendedor"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                activityError.representative_name &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={spaLocale}>
              <KeyboardDatePicker
                clearable
                style={{ margin: 16, marginLeft: 8 }}
                label="Fecha de actividad"
                value={activity.action_date}
                onChange={(date) =>
                  setActivity((prevState) => {
                    return {
                      ...prevState,
                      action_date: date,
                    };
                  })
                }
                format="dd/MM/yyyy"
              />
            </MuiPickersUtilsProvider>
            <TextField
              select
              error={activityError.executed}
              onChange={handleChange}
              value={activity.executed}
              name="executed"
              label="Estado"
              className={classes.textField}
              helperText=""
              margin="normal"
            >
              {executedValues.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </div>
        )}
      </DialogContent>
      <DialogActions>
        {!edit && (activity.id > 0 ?
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        :
          <>
            <Button
              disabled
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        )}
        {edit && (
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleCancelEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Cancelar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleSave}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Guardar
            </Button>
          </>
        )}
      </DialogActions>
    </div>
  );
}

