import React, { useState, useContext} from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import { isEmpty } from "validator";
//Context
import { UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider"
//RBAC
import Can from "components/Can/Can"
//General variables
import { statusValues } from "variables/general"

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  margin: {
    margin: theme.spacing(1),
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function RepresentativeDialog(props) {
  const { onAdd } = props;

  const classes = useStyles();

  const userPrivilege = useContext(UserPrivilegeContext);

  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [open, setOpen] = useState(false);
  const [representative, setRepresentative] = useState({
    fname: "",
    lname: "",
    contact: "",
    note: "",
    status: 1,
  });
  const [representativeError, setRepresentativeError] = useState({
    fname: false,
    lname: false,
    contact: false,
    note: false,
    status: false,
  });

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "fname") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        fname: value,
        }
      });
    }

    if (name === "lname") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        lname: value,
        }
      });
    }

    if (name === "contact") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        contact: value,
        }
      });
    }

    if (name === "note") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        note: value,
        }
      });
    }

    if (name === "status") {
      setRepresentative((prevState) => {
        return {
        ...prevState,
        status: value,
        }
      });
    }
    
    validateField(!isEmpty(value.toString()), name);
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setRepresentativeError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setRepresentativeError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(representative.fname), "fname");
    const check2 = validateField(!isEmpty(representative.lname), "lname");
    return (check1 && check2)
  }

  function resetFields() {
    setRepresentative({
      fname: "",
      lname: "",
      contact: "",
      note: "",
      status: 1,
    });
    setRepresentativeError({
      fname: false,
      lname: false,
      contact: false,
      note: false,
      status: false,
    });
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    resetFields();
    setOpen(false);
  };

  const handleClick = () => {
    if (!validateAll()) {
      return;
    }
    onAdd(representative);
    resetFields();
    setOpen(false);
  };

  return (
    <div>
      <Can 
          role={userPrivilege} 
          perform="representatives:edit"
          data={[]} 
          yes={() => {
            setIsButtonDisabled(false);
            return null
          }}
          no={() => {
            setIsButtonDisabled(true);
            return null
          }}
      />
      <Button
        disabled={isButtonDisabled}
        variant="contained"
        size="large"
        color="primary"
        onClick={handleClickOpen}
        className={classes.margin}
      >
        Agregar
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Agregar Vendedor</DialogTitle>
        <DialogContent>
          <div className={classes.root}>
            <div>
              <TextField
                error={representativeError.fname}
                onChange={handleChange}
                value={representative.fname}
                name="fname"
                label="Nombre"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                  representativeError.fname && "Este campo no puede estar vacio"
                }
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                error={representativeError.lname}
                onChange={handleChange}
                value={representative.lname}
                name="lname"
                label="Apellido"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                    representativeError.lname && "Este campo no puede estar vacio"
                  }
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                multiline
                onChange={handleChange}
                value={representative.contact}
                name="contact"
                label="Email"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                multiline
                onChange={handleChange}
                value={representative.note}
                name="note"
                label="Nota"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                select
                error={representativeError.status}
                onChange={handleChange}
                value={representative.status}
                name="status"
                label="Estado"
                className={classes.textField}
                helperText=""
                margin="normal"
              >
                {statusValues.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={handleClick} color="primary">
            Agregar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

