import React, { useEffect, useState, useContext } from "react";
import MUIDataTable from "mui-datatables";
import MUIDataTableSpanish from "variables/MUIDataTablesSpanish";
import RepresentativeModal from "components/RepresentativeModal/RepresentativeModal";
import RepresentativeForm from "components/RepresentativeForm/RepresentativeForm";
import ErrorModal from "components/ErrorModal/ErrorModal";
import {
  customCellStyleSmall,
  customCellStyleMedium,
  customCellStyleLarge,
  customCellStyleStatus,
} from "variables/cellStyles";
//Context
import { RepresentativeTableContext, UpdateRepresentativeTableContext, UpdateErrorStatusContext, UserContext } from "components/GeneralContextProvider/GeneralContextProvider"
import {
  getAllRepresentatives,
  createRepresentative,
  updateRepresentative,
  removeRepresentative,
} from "repositories/RepresentativeRepository";

const RepresentativeTable = () => {
  const [editedRepresentative, setEditedRepresentative] = useState({});
  const [edit, setEdit] = useState(false);
  const [forbiddenQuery, setForbiddenQuery] = useState(false);

  const tableState = useContext(RepresentativeTableContext);
  const setTableState = useContext(UpdateRepresentativeTableContext);
  const setErrorStatus = useContext(UpdateErrorStatusContext);
  const userToken = useContext(UserContext);

  useEffect(() => {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        loading: true,
      };
    });
    getAllRepresentatives(setTableData, onQueryError, userToken);
  }, []);

  function addRepresentative(representative) {
    createRepresentative(
      representative,
      onQuerySuccess,
      onQueryError,
      userToken
    );
  }

  function editRepresentative(representative) {
    updateRepresentative(
      representative,
      onQuerySuccess,
      onQueryError,
      userToken
    );
  }

  // function deleteRepresentatives(rowsDeleted, data) {
  //   rowsDeleted.data.map((entry) => {
  //     const index = entry.dataIndex;
  //     const representativeId = tableState.data[index].id;
  //     removeRepresentative(
  //       representativeId,
  //       onQuerySuccess,
  //       onQueryError,
  //       userToken
  //     );
  //   });
  // }

  function deleteRepresentative(representative) {
    let representativeId = representative.id;
      removeRepresentative(
        representativeId,
        onQuerySuccess,
        onQueryError,
        userToken
      );
  }

  function handleRowClick(rowData, rowMeta) {
    const representative = tableState.data[rowMeta.dataIndex];
    setEditedRepresentative(representative);
    setEdit(true);
  }

  function onQuerySuccess(statusCode) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        loading: true,
      };
    });
    getAllRepresentatives(setTableData, onQueryError, userToken);
  }

  function onQueryError(error) {
    let statusCode = parseInt(error.message);
    if (statusCode===403) {
      setForbiddenQuery(true);
    } else if (statusCode===401) {
      //Here goes some modal about unauthorized operation
    } else {
      setErrorStatus(true);
    }
  }

  function setTableData(fetchedData) {
    setTableState({ data: fetchedData, loading: false });
  }

  function closeEditModal() {
    setEdit(false);
  }

  const columns = [
    {
      name: "fname",
      label: "Nombre",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleSmall,
      },
    },
    {
      name: "lname",
      label: "Apellido",
      options: {
        sortDirection: 'desc',
        filter: true,
        sort: true,
        customBodyRender: customCellStyleSmall,
      },
    },
    {
      name: "contact",
      label: "Email",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
    {
      name: "note",
      label: "Nota",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
    {
      name: "status",
      label: "Estado",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleStatus,
      },
    },
  ];

  const options = {
    ...MUIDataTableSpanish,
    selectableRows: "none",
    filterType: "textField",
    filter: true,
    sort: true,
    rowsPerPage: 5,
    rowsPerPageOptions: [],
    onRowClick: handleRowClick,
    onRowsDelete: false,
  };

  return (
    <div>
      <MUIDataTable
        title={"Lista de Vendedores"}
        data={tableState.data}
        columns={columns}
        options={options}
      />
      <RepresentativeModal onAdd={addRepresentative} />
      {/* {edit && (
        <RepresentativeEditModal
          onEdit={editRepresentative}
          editedRepresentative={editedRepresentative}
          setOpen={setEdit}
        />
      )} */}
      <RepresentativeForm onEdit={editRepresentative} onDelete={deleteRepresentative} editedRepresentative={editedRepresentative} />
      {forbiddenQuery && <ErrorModal setOpen={setForbiddenQuery} />}
    </div>
  );
};

export default RepresentativeTable;

