import React, { useContext } from "react";
import { useGoogleLogout } from "react-google-login";
import { useHistory } from "react-router-dom";
//Context
import { UpdateUserContext } from "components/GeneralContextProvider/GeneralContextProvider"

const clientId =
  "816786373130-qia4ks1egpir1hmq1s3n3ks61blvcr6g.apps.googleusercontent.com";

function Logout() {

  const setUserToken = useContext(UpdateUserContext);

  let history = useHistory();

  const onLogoutSuccess= (res) => {
    console.log("Logout Success!")
    setUserToken("");
    history.push("/login");
  };

  const onFailure = () => {
    console.log('Handle failure cases')
  }

  const { signOut } = useGoogleLogout({
    clientId,
    onLogoutSuccess,
    onFailure
  });

  return (
    <a
        onClick={signOut}
      >
        Cerrar sesin
    </a>
  );
}
 


export default Logout;
