import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { isEmpty } from "validator";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    marginTop: "50px",
    align: "left",
    borderRadius: "1.5em",
    boxShadow: "0px 11px 35px 2px rgba(0, 0, 0, 0.14)",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  button: {
    margin: "10px",
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  table: {
    minWidth: 600,
  },
  activitySummary: {
    marginTop: "20px"
  },
}));

export default function ClientActivitySummary(props) {
  const { lastActivities, nextActivities } = props;

  const classes = useStyles();

  return (
    <div className={classes.mainDiv}>
      <DialogTitle id="form-dialog-title">Resumen de Actividades</DialogTitle>
      <DialogContent>
        <div>
          <h6>ltimas Actividades</h6>
          {lastActivities.length === 0 && "No hay actividades"}
          {lastActivities.length > 0 && <>
          <TableContainer component={Paper}>
            <Table
              className={classes.table}
              size="small"
              aria-label="a dense table"
            >
              <TableHead>
                <TableRow>
                  <TableCell>Vendedor</TableCell>
                  <TableCell>Estado actual</TableCell>
                  <TableCell>Prximo paso</TableCell>
                  <TableCell colSpan={15}>Fecha</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {lastActivities.map((activity) => (
                  <TableRow key={activity.id}>
                    <TableCell component="th" scope="row">
                      {activity.fname + " " + activity.lname}
                    </TableCell>
                    <TableCell component="th" scope="row">
                      {activity.current_status}
                    </TableCell>
                    <TableCell component="th" scope="row">
                      {activity.description}
                    </TableCell>
                    <TableCell component="th" scope="row">
                      {new Date(activity.action_date).toLocaleDateString("spa", {
                        year: "numeric",
                        month: "short",
                        day: "numeric",
                      })}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          </> }

          <h6 className={classes.activitySummary}>Prximas Actividades</h6>
          {nextActivities.length === 0 && "No hay actividades"}
          {nextActivities.length > 0 && <>
          <TableContainer component={Paper}>
            <Table
              className={classes.table}
              size="small"
              aria-label="a dense table"
            >
              <TableHead>
                <TableRow>
                  <TableCell>Vendedor</TableCell>
                  <TableCell>Estado actual</TableCell>
                  <TableCell>Prximo paso</TableCell>
                  <TableCell colSpan={15}>Fecha</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {nextActivities.map((activity) => (
                  <TableRow key={activity.id}>
                    <TableCell component="th" scope="row">
                      {activity.fname + " " + activity.lname}
                    </TableCell>
                    <TableCell component="th" scope="row">
                      {activity.current_status}
                    </TableCell>
                    <TableCell component="th" scope="row">
                      {activity.description}
                    </TableCell>
                    <TableCell component="th" scope="row">
                      {new Date(activity.action_date).toLocaleDateString("spa", {
                        year: "numeric",
                        month: "short",
                        day: "numeric",
                      })}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          </> }
        </div>
      </DialogContent>
      <DialogActions></DialogActions>
    </div>
  );
}
