import React from "react";
//General variables
import { privilegeValues, statusValues } from "variables/general"

const customCellStyleLarge = (val) => {
  let parentStyle = {
    position: "relative",
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    boxSizing: "border-box",
    display: "block",
    width: "250px",
  };
  let cellStyle = {
    boxSizing: "border-box",
    overflow: "hidden",
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
  };
  return (
    <div style={parentStyle}>
      <div style={cellStyle}>{val}</div>
    </div>
  );
};

