const customCellStyleDate = (val) => {
  let parentStyle = {
    position: "relative",
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    boxSizing: "border-box",
    display: "block",
    width: "100px",
  };
  let cellStyle = {
    boxSizing: "border-box",
    overflow: "hidden",
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
  };
  return (
    <div style={parentStyle}>
      <div style={cellStyle}>
      {new Date(val).toLocaleDateString('spa', {
           year: 'numeric',
           month: 'short',
           day: 'numeric'
         })}
      </div>
    </div>
  );
};

