import React, { useState, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import { isEmpty } from "validator";
//Context
import { UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider";
//RBAC
import Can from "components/Can/Can";
//General variables
import { privilegeValues } from "variables/general"

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  margin: {
    margin: theme.spacing(1),
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function AppUserDialog(props) {
  const { onAdd } = props;

  const classes = useStyles();

  const userPrivilege = useContext(UserPrivilegeContext);

  const [open, setOpen] = useState(false);
  const [appUser, setAppUser] = useState({
    id: 0,
    email: "",
    privilege_level: "",
    note: "",
  });
  const [appUserError, setAppUserError] = useState({
    email: false,
    note: false,
  });

  const [isButtonDisabled, setIsButtonDisabled] = useState(true);

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "email") {
      setAppUser((prevState) => {
        return {
          ...prevState,
          email: value,
        };
      });
    }

    if (name === "privilege_level") {
      setAppUser((prevState) => {
        return {
          ...prevState,
          privilege_level: value,
        };
      });
    }

    if (name === "note") {
      setAppUser((prevState) => {
        return {
          ...prevState,
          note: value,
        };
      });
    }

    validateField(!isEmpty(value.toString()), "email");
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setAppUserError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setAppUserError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(appUser.email), "email");
    return check1;
  }

  function resetFields() {
    setAppUser({
      id: 0,
      email: "",
      privilege_level: "",
      note: "",
    });
    setAppUserError({
      email: false,
    });
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    resetFields();
    setOpen(false);
  };

  const handleClick = () => {
    if (!validateAll()) {
      return;
    }
    onAdd(appUser);
    resetFields();
    setOpen(false);
  };

  return (
    <div>
      <Can
        role={userPrivilege}
        perform="appUsers:edit"
        data={[]}
        yes={() => {
          setIsButtonDisabled(false);
          return null;
        }}
        no={() => {
          setIsButtonDisabled(true);
          return null;
        }}
      />
      <Button
        disabled={isButtonDisabled}
        variant="contained"
        size="large"
        color="primary"
        onClick={handleClickOpen}
        className={classes.margin}
      >
        Agregar
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Agregar Usuario</DialogTitle>
        <DialogContent>
          <div className={classes.root}>
            <div>
              <TextField
                error={appUserError.email}
                onChange={handleChange}
                value={appUser.email}
                name="email"
                label="Email"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                  appUserError.email && "Este campo no puede estar vacio"
                }
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                select
                onChange={handleChange}
                value={appUser.privilege_level}
                name="privilege_level"
                label="Privilegio"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              >
                {privilegeValues.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>
              <TextField
                multiline
                onChange={handleChange}
                value={appUser.note}
                name="note"
                label="Nota"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={handleClick} color="primary">
            Agregar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

