import React, { useState, useEffect, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
//Context
import { UserNameContext, UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider"
import { isEmpty } from "validator";
//RBAC
import Can from "components/Can/Can"
//General variables
import { privilegeValues } from "variables/general"

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    marginTop: "50px",
    align: "left",
    borderRadius: "1.5em",
    boxShadow: "0px 11px 35px 2px rgba(0, 0, 0, 0.14)",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  button: {
    margin: "10px",
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function AppUserForm(props) {
  const { onEdit, onDelete, editedAppUser } = props;

  const classes = useStyles();

  //Name of current user to be used in the logs
  const userName = useContext(UserNameContext);
  //Privilege level of the current user
  const userPrivilege = useContext(UserPrivilegeContext);

  const [appUser, setAppUser] = useState({
    id: 0,
    email: "",
    privilege_level: "",
    note: "",
  });
  const [edit, setEdit] = useState(false);
  const [appUserError, setAppUserError] = useState({
    email: false,
    note: false,
  });
  
  //This one is to define whether some buttons are disabled or not based on privileges
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);

  useEffect(() => {
    setAppUser(editedAppUser);
  }, [editedAppUser]);

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "email") {
      setAppUser((prevState) => {
        return {
        ...prevState,
        email: value,
        }
      });
    }

    if (name === "privilege_level") {
      setAppUser((prevState) => {
        return {
        ...prevState,
        privilege_level: value,
        }
      });
    }

    if (name === "note") {
      setAppUser((prevState) => {
        return {
        ...prevState,
        note: value,
        }
      });
    }

    validateField(!isEmpty(value.toString()), "email");
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setAppUserError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setAppUserError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(appUser.email), "email");
    return check1;
  }

  function resetFields() {
    setAppUser({
      id: 0,
      email: "",
      privilege_level: "",
      note: "",
    });
    setAppUserError({
      email: false,
    });
  }

  const handleSave = () => {
    if (!validateAll()) {
      return;
    }
    onEdit(appUser);
    setEdit(false);
    resetFields();
  };

  const handleEdit = () => {
    setEdit(true);
  };

  const handleDelete = () => {
    onDelete(appUser);
    resetFields();
  };

  const handleCancelEdit = () => {
    setEdit(false);
    resetFields();
    setAppUser(editedAppUser);
  };

  return (
    <div className={classes.mainDiv}>
      <Can 
          role={userPrivilege} 
          perform="appUsers:edit"
          data={[]} 
          yes={() => {
            setIsButtonDisabled(false);
            return null
          }}
          no={() => {
            setIsButtonDisabled(true);
            return null
          }}
      />
      <DialogTitle id="form-dialog-title">
        Informacin de Usuario
      </DialogTitle>
      <DialogContent>
        {!edit && (
            <div>
              <TextField
                disabled
                value={appUser.email}
                name="email"
                label="Email"
                style={{ margin: 8 }}
                placeholder=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                disabled
                value={privilegeValues[appUser.privilege_level || 0].label}
                name="privilege_level"
                label="Privilegio"
                style={{ margin: 8 }}
                placeholder=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                disabled
                multiline
                value={appUser.note}
                name="note"
                label="Nota"
                style={{ margin: 8 }}
                placeholder=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </div>
        )}

        {edit && (
         <div>
         <TextField
           error={appUserError.email}
           onChange={handleChange}
           value={appUser.email}
           name="email"
           label="Email"
           style={{ margin: 8 }}
           placeholder=""
           helperText={
             appUserError.email && "Este campo no puede estar vacio"
           }
           fullWidth
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           select
           onChange={handleChange}
           value={appUser.privilege_level}
           name="privilege_level"
           label="Privilegio"
           style={{ margin: 8 }}
           placeholder=""
           helperText=""
           fullWidth
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         >
         {privilegeValues.map((option) => (
             <MenuItem key={option.value} value={option.value}>
               {option.label}
             </MenuItem>
           ))}
         </TextField>
         <TextField
           multiline
           onChange={handleChange}
           value={appUser.note}
           name="note"
           label="Nota"
           style={{ margin: 8 }}
           placeholder=""
           helperText=""
           fullWidth
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
       </div>
        )}
      </DialogContent>
      <DialogActions>
        {!edit && (appUser.id > 0 ?
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        :
          <>
            <Button
              disabled
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        )}
        {edit && (
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleCancelEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Cancelar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleSave}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Guardar
            </Button>
          </>
        )}
      </DialogActions>
    </div>
  );
}
