import { baseUrl } from "variables/general.js"

const getAllClients = (setTableData, onError, token) => {
  const queryUrl = baseUrl + "Clients";

  fetch(queryUrl, {
    method: "GET",
    headers: {
      "Authorization": [token],
    },
  })
    .then((res) => {
      if (!res.ok) {
        throw Error(res.status);
      }
      return res;
    })
    .then((res) => res.json())
    .then((fetchedData) => {
      setTableData(fetchedData);
    })
    .catch((err) => {
      onError(err);
    });
};

const getLastClientActivities = (clientCode, setTableData, onError, token) => {
  let today = new Date();
  const params = { 
    lastActivities: 1, 
    thresholdDate: today.toLocaleDateString("fr-CA"),
    code: clientCode};
  let queryUrl = new URL(baseUrl + "Clients");
  queryUrl.search = new URLSearchParams(params).toString();

  fetch(queryUrl, {
    method: "GET",
    headers: {
      "Authorization": [token],
    },
  })
    .then((res) => {
      if (!res.ok) {
        throw Error(res.status);
      }
      return res;
    })
    .then((res) => res.json())
    .then((fetchedData) => {
      setTableData(fetchedData);
    })
    .catch((err) => {
      onError(err);
    });
};

