import React, { useEffect, useState, useContext } from "react";
import MUIDataTable, { ExpandButton } from "mui-datatables";
import MUIDataTableSpanish from "variables/MUIDataTablesSpanish";
import ActivityModal from "components/ActivityModal/ActivityModal";
import ActivityForm from "components/ActivityForm/ActivityForm";
import ErrorModal from "components/ErrorModal/ErrorModal";
import ScrollableTabPanel from "components/ScrollableTabPanel/ScrollableTabPanel";
import {
  ActivityTableContext,
  UpdateActivityTableContext,
  UpdateErrorStatusContext,
  UserContext,
} from "components/GeneralContextProvider/GeneralContextProvider";
import {
  customCellStyleMedium,
  customCellStyleLarge,
  customCellStyleDate,
  customCellStyleType,
} from "variables/cellStyles";
import {
  getAllActivities,
  getLaggingActivities,
  getTodayActivities,
  getTomorrowActivities,
  getWeekActivities,
  getNextWeekActivities,
  getMonthActivities,
  getNextMonthActivities,
  getFilteredActivities,
  createActivity,
  updateActivity,
  removeActivity,
} from "repositories/ActivityRepository";
import { getAllClients } from "repositories/ClientRepository";
import { getAllRepresentatives } from "repositories/RepresentativeRepository";
import { CircularProgress, Typography } from "@material-ui/core";

const Activity = () => {
  const [edit, setEdit] = useState(false);

  const [editedActivity, setEditedActivity] = useState({});
  const [currentView, setCurrentView] = useState(0);
  const [forbiddenQuery, setForbiddenQuery] = useState(false);
  const [clientList, setClientList] = useState([]);
  const [representativeList, setRepresentativeList] = useState([]);

  const [searchBarString, setSearchBarString] = useState("!all");
  const [currentTablePage, setCurrentTablePage] = useState(0);

  const tableState = useContext(ActivityTableContext);
  const setTableState = useContext(UpdateActivityTableContext);
  const setErrorStatus = useContext(UpdateErrorStatusContext);

  const userToken = useContext(UserContext);

  useEffect(() => {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        loading: true,
      };
    });
    getTodayActivities(setTableDataToday, onQueryError, userToken);
    getTomorrowActivities(setTableDataTomorrow, onQueryError, userToken);
    getWeekActivities(setTableDataWeek, onQueryError, userToken);
    getNextWeekActivities(setTableDataNextWeek, onQueryError, userToken);
    getMonthActivities(setTableDataMonth, onQueryError, userToken);
    getNextMonthActivities(setTableDataNextMonth, onQueryError, userToken);
    getAllClients(setClientList, onQueryError, userToken);
    getAllRepresentatives(setRepresentativeList, onQueryError, userToken);
  }, []);

  useEffect(() => {
    updateCurrentTable();
  }, [currentView]);

  function updateCurrentTable() {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        loading: true,
      };
    });
    let filter = {};
    switch (currentView) {
      case 0:
        getTodayActivities(setTableDataToday, onQueryError, userToken);
        break;
      case 1:
        getTomorrowActivities(setTableDataTomorrow, onQueryError, userToken);
        break;
      case 2:
        getWeekActivities(setTableDataWeek, onQueryError, userToken);
        break;
      case 3:
        getNextWeekActivities(setTableDataNextWeek, onQueryError, userToken);
        break;
      case 4:
        getMonthActivities(setTableDataMonth, onQueryError, userToken);
        break;
      case 5:
        getNextMonthActivities(setTableDataNextMonth, onQueryError, userToken);
        break;
      case 6:
        filter = {
          type: "search",
          searchString: "!all",
          page: 0,
        };
        getLaggingActivities(setTableDataLagging, filter, onQueryError, userToken);
        break;
      case 7:
        filter = {
          type: "search",
          searchString: "!all",
          page: 0,
        };
        getFilteredActivities(setTableDataAll, filter, onQueryError, userToken);
        break;
      default:
        getTodayActivities(setTableDataToday, onQueryError, userToken);
    }
  }

  function addActivity(activity) {
    createActivity(activity, onQuerySuccess, onQueryError, userToken);
  }

  function editActivity(activity) {
    updateActivity(activity, onQuerySuccess, onQueryError, userToken);
  }

  function deleteActivity(activity) {
    let activityId = activity.id;
    removeActivity(activityId, onQuerySuccess, onQueryError, userToken);
  }

  function handleTableChange(action, tableState) {
    setCurrentTablePage(tableState.page);
    setSearchBarString(tableState.searchText);
    if (action === "search" || action === "changePage") {
      let filter = {
        type: action,
        searchString: tableState.searchText,
        page: tableState.page,
      };
      switch (currentView) {
        case 6:
          getLaggingActivities(setTableDataLagging, filter, onQueryError, userToken);
          break;
        case 7:
          getFilteredActivities(setTableDataAll, filter, onQueryError, userToken);
          break;
        default:
      }
      
    }
  }

  function handleRowClick(rowData, rowMeta) {
    let activity = [];
    switch (currentView) {
      case 0:
        activity = tableState.dataToday[rowMeta.dataIndex];
        break;
      case 1:
        activity = tableState.dataTomorrow[rowMeta.dataIndex];
        break;
      case 2:
        activity = tableState.dataWeek[rowMeta.dataIndex];
        break;
      case 3:
        activity = tableState.dataNextWeek[rowMeta.dataIndex];
        break;
      case 4:
        activity = tableState.dataMonth[rowMeta.dataIndex];
        break;
      case 5:
        activity = tableState.dataNextMonth[rowMeta.dataIndex];
        break;
      case 6:
        activity = tableState.dataLagging[rowMeta.dataIndex];
        break;
      case 7:
        activity = tableState.dataAll[rowMeta.dataIndex];
        break;
      default:
        activity = tableState.dataToday[rowMeta.dataIndex];
    }
    setEditedActivity(activity);
    setEdit(true);
  }

  function onQuerySuccess(statusCode) {
    if (statusCode === 200) {
      setTableState((prevValue) => {
        return {
          ...prevValue,
          loading: true,
        };
      });
      let filter = {};
      switch (currentView) {
        case 0:
          getTodayActivities(setTableDataToday, onQueryError, userToken);
          break;
        case 1:
          getTomorrowActivities(setTableDataTomorrow, onQueryError, userToken);
          break;
        case 2:
          getWeekActivities(setTableDataWeek, onQueryError, userToken);
          break;
        case 3:
          getNextWeekActivities(setTableDataNextWeek, onQueryError, userToken);
          break;
        case 4:
          getMonthActivities(setTableDataMonth, onQueryError, userToken);
          break;
        case 5:
          getNextMonthActivities(setTableDataNextMonth, onQueryError, userToken);
          break;
        case 6:
          filter = {
            type: "search",
            searchString: searchBarString,
            page: currentTablePage,
          };
          getLaggingActivities(setTableDataLagging, filter, onQueryError, userToken);
          break;
        case 7:
          filter = {
            type: "search",
            searchString: searchBarString,
            page: currentTablePage,
          };
          getFilteredActivities(setTableDataAll, filter, onQueryError, userToken);
          break;
        default:
          getTodayActivities(setTableDataToday, onQueryError, userToken);
      }
    } else if (statusCode === 403) {
      setForbiddenQuery(true);
    } else {
      //setErrorStatus(true);
    }
  }

  function onQueryError(error) {
    let statusCode = parseInt(error.message);
    if (statusCode === 403) {
      setForbiddenQuery(true);
    } else if (statusCode === 401) {
      //Here goes some modal about unauthorized operation
    } else {
      setErrorStatus(true);
    }
  }

  function setTableDataToday(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataToday: fetchedData,
        loading: false,
      };
    });
  }

  function setTableDataTomorrow(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataTomorrow: fetchedData,
        loading: false,
      };
    });
  }

  function setTableDataWeek(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataWeek: fetchedData,
        loading: false,
      };
    });
  }

  function setTableDataNextWeek(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataNextWeek: fetchedData,
        loading: false,
      };
    });
  }

  function setTableDataMonth(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataMonth: fetchedData,
        loading: false,
      };
    });
  }

  function setTableDataNextMonth(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataNextMonth: fetchedData,
        loading: false,
      };
    });
  }

  function setTableDataLagging(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataLagging: fetchedData.data,
        countLagging: fetchedData.count,
        loading: false,
      };
    });
  }

  function setTableDataAll(fetchedData) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        dataAll: fetchedData.data,
        countAll: fetchedData.count,
        loading: false,
      };
    });
  }

  const columns = [
    {
      name: "name",
      label: "Cliente",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
    {
      name: "client_code",
      label: "RIF",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleMedium,
      },
    },
    {
      name: "type",
      label: "Tipo",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleType,
      },
    },
    {
      name: "representative_name",
      label: "Vendedor asignado",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleMedium,
      },
    },
    {
      name: "action_date",
      label: "Fecha de actividad",
      options: {
        sortDirection: "desc",
        filter: false,
        sort: true,
        customBodyRender: customCellStyleDate,
      },
    },
    {
      name: "executed_string",
      label: "Ejecutado? (Si/No)",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleMedium,
        filterOptions: {
          logic: (executed, filters, row) => {
            const yes = "Ejecutado ejecutado Si si";
            const no = "'Por ejecutar' 'Por Ejecutar' 'por ejecutar' No no";
            if (yes.includes(filters)) return executed != 1;
            else if (no.includes(filters)) return executed != 0;
            else return true;
          },
        },
      },
    },
  ];

  const optionsCommon = {
    ...MUIDataTableSpanish,
    selectableRows: "none",
    filterType: "textField",
    filter: true,
    sort: true,
    rowsPerPage: 10,
    rowsPerPageOptions: [],
    onRowClick: handleRowClick,
    onRowsDelete: false,
  };

  const optionsAll = {
    ...optionsCommon,
    sort: false,
    filter: false,
    count: tableState.countAll,
    serverSide: true,
    onTableChange: handleTableChange,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(tableState.dataAll[dataIndex].executed);
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  const optionsToday = {
    ...optionsCommon,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(tableState.dataToday[dataIndex].executed);
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  const optionsTomorrow = {
    ...optionsCommon,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(
        tableState.dataTomorrow[dataIndex].executed
      );
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  const optionsWeek = {
    ...optionsCommon,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(tableState.dataWeek[dataIndex].executed);
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  const optionsNextWeek = {
    ...optionsCommon,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(
        tableState.dataNextWeek[dataIndex].executed
      );
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  const optionsMonth = {
    ...optionsCommon,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(
        tableState.dataMonth[dataIndex].executed
      );
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  const optionsNextMonth = {
    ...optionsCommon,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(
        tableState.dataNextMonth[dataIndex].executed
      );
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  const optionsLagging = {
    ...optionsCommon,
    sort: false,
    filter: false,
    count: tableState.countLagging,
    serverSide: true,
    onTableChange: handleTableChange,
    setRowProps: (row, dataIndex, rowIndex) => {
      const isTaskExecuted = Number(tableState.dataLagging[dataIndex].executed);
      if (isTaskExecuted === 1) {
        return {
          style: {
            textDecoration: "line-through",
            backgroundColor: "#efefef",
          },
        };
      }
    },
  };

  return (
    <div>
      <ScrollableTabPanel
        onViewChange={setCurrentView}
        table0={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Actividades para hoy
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataToday}
            columns={columns}
            options={optionsToday}
          />
        }
        table1={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Actividades para maana
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataTomorrow}
            columns={columns}
            options={optionsTomorrow}
          />
        }
        table2={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Actividades para la semana
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataWeek}
            columns={columns}
            options={optionsWeek}
          />
        }
        table3={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Actividades para la prxima semana
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataNextWeek}
            columns={columns}
            options={optionsNextWeek}
          />
        }
        table4={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Actividades del mes
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataMonth}
            columns={columns}
            options={optionsMonth}
          />
        }
        table5={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Actividades del siguiente mes
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataNextMonth}
            columns={columns}
            options={optionsNextMonth}
          />
        }
        table6={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Actividades rezagadas
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataLagging}
            columns={columns}
            options={optionsLagging}
          />
        }
        table7={
          <MUIDataTable
            title={
              <Typography variant="h6">
                Todas las actividades
                {tableState.loading && (
                  <CircularProgress
                    size={24}
                    style={{ marginLeft: 15, position: "relative", top: 4 }}
                  />
                )}
              </Typography>
            }
            data={tableState.dataAll}
            columns={columns}
            options={optionsAll}
          />
        }
      />
      <ActivityModal
        onAdd={addActivity}
        clients={clientList}
        representatives={representativeList}
      />
      <ActivityForm
        onEdit={editActivity}
        onDelete={deleteActivity}
        clients={clientList}
        representatives={representativeList}
        editedActivity={editedActivity}
      />
      {forbiddenQuery && <ErrorModal setOpen={setForbiddenQuery} />}
    </div>
  );
};

export default Activity;

