import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import MenuItem from "@material-ui/core/MenuItem";
import { isEmpty } from "validator";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import spaLocale from "date-fns/locale/es";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from "@material-ui/pickers";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
}));

export default function ActivityDialog(props) {
  const { onAdd, clients } = props;

  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [activity, setActivity] = useState({
    name: "",
    client_code: "",
    current_status: "",
    description: "",
    representative_name: "",
    representative_id: "",
    action_date: new Date(),
    executed: 0,
  });
  const [activityError, setActivityError] = useState({
    name: false,
    client_code: false,
    current_status: false,
    description: false,
    representative_name: false,
    action_date: false,
    executed: false,
  });

  //These two are for the autocomplete component
  const [clientAutocompleteValue, setClientAutocompleteValue] = useState({});
  const [clientAutocompleteInput, setClientAutocompleteInput] = useState("");

  const executedValues = [
    {
      value: 0,
      label: "Por ejecutar",
    },
    {
      value: 1,
      label: "Ejecutado",
    },
  ];

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "current_status") {
      setActivity((prevState) => {
        return {
        ...prevState,
        current_status: value,
      }
      });
    }

    if (name === "description") {
      setActivity((prevState) => {
      return {
        ...prevState,
        description: value,
      }
      });
    }

    // if (name === "owner_representative_name") {
    //   const filteredRepresentative = representatives.filter(
    //     (representative) =>
    //       representative.fname + " " + representative.lname === value
    //   );
    //   setActivity((prevState) => {
    //   return {
    //     ...prevState,
    //     owner_representative_name: value,
    //     owner_representative_id: filteredRepresentative[0].id,
    //   }
    //   });
    // }

    if (name === "action_date") {
      setActivity((prevState) => {
      return {
        ...prevState,
        action_date: value,
      }
      });
    }

    if (name === "executed") {
      setActivity((prevState) => {
        return {
        ...prevState,
        executed: value,
        }
      });
    }
    
    validateField(!isEmpty(value.toString()), name);
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setActivityError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setActivityError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(activity.name), "name");
    const check2 = validateField(!isEmpty(activity.description), "description");
    const check3 = validateField(
      !isEmpty(activity.current_status),
      "current_status"
    );
    const check4 = validateField(
      !isEmpty(activity.representative_name),
      "representative_name"
    );
    return check1 && check2 && check3 && check4;
  }

  function resetFields() {
    setActivity({
      name: "",
      client_code: "",
      current_status: "",
      description: "",
      representative_name: "",
      representative_id: "",
      action_date: new Date(),
      executed: 0,
    });
    setActivityError({
      name: false,
      client_code: false,
      current_status: false,
      description: false,
      representative_name: false,
      action_date: false,
      executed: false,
    });
    setClientAutocompleteValue({});
    setClientAutocompleteInput("");
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    resetFields();
    setOpen(false);
  };

  const handleClick = () => {
    if (!validateAll()) {
      return;
    }
    onAdd(activity);
    resetFields();
    setOpen(false);
  };

  return (
    <div>
      <Button
        variant="contained"
        size="large"
        color="primary"
        onClick={handleClickOpen}
        className={classes.margin}
      >
        Agregar
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Agregar Actividad</DialogTitle>
        <DialogContent>
          <div className={classes.root}>
            <Autocomplete
              value={clientAutocompleteValue || ""}
              onChange={(event, newClient) => {
                setClientAutocompleteValue(newClient);
                console.log(newClient);
                if (newClient) {
                  setActivity((prevState) => {
                    return {
                      ...prevState,
                      client_code: newClient.client_code,
                      name: newClient.client_name,
                      representative_name: newClient.owner_representative_name,
                    };
                  });
                }
              }}
              inputValue={clientAutocompleteInput}
              onInputChange={(event, input) => {
                setClientAutocompleteInput(input);
              }}
              options={clients}
              getOptionLabel={(client) => client.client_name || ""}
              name="name"
              label="Cliente"
              placeholder=""
              fullWidth
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Clientes"
                  margin="normal"
                  style={{ margin: 8 }}
                  InputLabelProps={{ shrink: true }}
                />
              )}
            />
            <TextField
              disabled
              onChange={handleChange}
              value={activity.client_code}
              name="client_code"
              label="RIF"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              multiline
              error={activityError.current_status}
              onChange={handleChange}
              value={activity.current_status}
              name="current_status"
              label="Estado actual"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                activityError.current_status &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              multiline
              error={activityError.description}
              onChange={handleChange}
              value={activity.description}
              name="description"
              label="Prximo paso"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                activityError.description && "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              disabled
              error={activityError.representative_name}
              onChange={handleChange}
              value={activity.representative_name}
              name="representative_name"
              label="Vendedor"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={spaLocale}>
              <KeyboardDatePicker
                clearable
                style={{ margin: 16, marginLeft: 8 }}
                label="Fecha de actividad"
                value={activity.action_date}
                onChange={(value) => {
                  setActivity((prevState) => {
                    return {
                      ...prevState,
                      action_date: value,
                    };
                  })
                }}
                format="dd/MM/yyyy"
              />
            </MuiPickersUtilsProvider>

            <TextField
              select
              error={activityError.executed}
              onChange={handleChange}
              value={activity.executed}
              name="executed"
              label="Ejecutado?"
              className={classes.textField}
              helperText=""
              margin="normal"
            >
              {executedValues.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={handleClick} color="primary">
            Agregar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

