import React, { useState, useContext } from "react";
import { useGoogleLogin } from "react-google-login";
import GoogleButton from "react-google-button";
import { useHistory } from "react-router-dom";
//Context
import { UpdateUserContext, UpdateUserNameContext, UpdateUserImageContext, UpdateUserEmailContext, UpdateUserPrivilegeContext, UserPrivilegeContext, UpdateErrorStatusContext } from "components/GeneralContextProvider/GeneralContextProvider"
import { refreshTokenSetup } from "variables/refreshTokenSetup";
//AppUser repository
import { getAppUserPrivilege } from "repositories/AppUserRepository"

const clientId = "816786373130-qia4ks1egpir1hmq1s3n3ks61blvcr6g.apps.googleusercontent.com"

function Login() {

  //This function is all about authentication. First to authenticate with Google, then to authenticate with the application. 

  const setUserToken = useContext(UpdateUserContext);
  const setUserName = useContext(UpdateUserNameContext);
  const setUserEmail = useContext(UpdateUserEmailContext);
  const setUserImage = useContext(UpdateUserImageContext);
  const setUserPrivilege = useContext(UpdateUserPrivilegeContext);
  const setErrorStatus = useContext(UpdateErrorStatusContext);

  const userPrivilege = useContext(UserPrivilegeContext);

  //Store Google's authentication response in a state variable
  const [ googleResponse, setGoogleResponse ] = useState();

  let history = useHistory();

  //On successful Google authentication, proceed to authenticate with the app. 
  const onSuccess = (res) => {
    setGoogleResponse(res);
    //Search for the user in the database
    let body = { email: res.profileObj.email }
    let userToken = res.tokenId;
    console.log("onGoogleAuthSuccess!!")
    getAppUserPrivilege(body, onAppAuthSuccess, onAppAuthFailure, onQueryError, userToken) 
  };

  //If user is authorized to use the app
  const onAppAuthSuccess = (user) => {
    let userPrivilege = user.user_privilege;
    if(Number(userPrivilege) >= 0) {
      refreshTokenSetup(googleResponse, setUserToken);
      setUserToken(googleResponse.tokenId);
      setUserName(googleResponse.profileObj.name);
      setUserEmail(googleResponse.profileObj.email);
      setUserImage(googleResponse.profileObj.imageUrl);
      setUserPrivilege(user.privilege_level);
      console.log("Login Success !");
      history.push("/admin/dashboard");
    } 
  }
 
  //If user is not authorized to use the app
  const onAppAuthFailure = () => {
    alert("Usuario no autorizado !");
    console.log("Login Failed !");
  }

  //On Google authentication failure
  const onFailure = (res) => {
    console.log("Login failed res:", res);
  };

  const { signIn } = useGoogleLogin({
    onSuccess,
    onFailure,
    clientId,
    //isSignedIn: true,
    accessType: "offline",
  });

  function onQueryError(error) {
    let statusCode = parseInt(error.message);
    setErrorStatus(true);
  }

  return <GoogleButton label="Iniciar sesin con Google" onClick={signIn} />;
}

export default Login;
