import React, { useState, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import { isEmpty } from "validator";
//Context
import { UserPrivilegeContext, UserNameContext } from "components/GeneralContextProvider/GeneralContextProvider"
//RBAC
import Can from "components/Can/Can"

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  button: {
    margin: theme.spacing(1),
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function ClientDialog(props) {
  const { onAdd, representatives } = props;

  const classes = useStyles();

  const userName = useContext(UserNameContext);
  const userPrivilege = useContext(UserPrivilegeContext);

  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [open, setOpen] = useState(false);
  const [client, setClient] = useState({
    client_name: "",
    client_code: "",
    owner_representative_name: "",
    capturing_representative_name: "",
    contact: "",
    note: "",
    products: "",
    type: 1,
    status: 1,
  });
  const [clientError, setClientError] = useState({
    name: false,
    code: false,
    owner_representative_name: false,
    capturing_representative_name: false,
    contact: false,
    note: false,
    products: false,
    type: false,
    status: false,
  });

  const statusValues = [
    {
      value: 0,
      label: "Inactivo",
    },
    {
      value: 1,
      label: "Activo",
    },
  ];

  const typeValues = [
    {
      value: 1,
      label: "Ideal",
    },
    {
      value: 0,
      label: "No ideal",
    },
  ];

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "client_name") {
      setClient((prevState) => {
        return {
        ...prevState,
        client_name: value,
      };
    });
    }

    if (name === "client_code") {
      setClient((prevState) => {
      return {
        ...prevState,
        client_code: value,
      };
    });
    }

    if (name === "owner_representative_name") {
      const filteredRepresentative = representatives.filter(
        (representative) =>
          representative.fname + " " + representative.lname === value
      );
      setClient((prevState) => {
      return {
        ...prevState,
        owner_representative_name: value,
        owner_representative_id: filteredRepresentative[0].id,
      }
      });
    }

    if (name === "capturing_representative_name") {
      const filteredRepresentative = representatives.filter(
        (representative) =>
          representative.fname + " " + representative.lname === value
      );
      setClient((prevState) => {
      return {
        ...prevState,
        capturing_representative_name: value,
        capturing_representative_id: filteredRepresentative[0].id,
      }
      });
    }

    if (name === "contact") {
      setClient((prevState) => {
      return {
        ...prevState,
        contact: value,
      };
    });
    }

    if (name === "note") {
      setClient((prevState) => {
      return {
        ...prevState,
        note: value,
      };
    });
    }

    if (name === "products") {
      setClient((prevState) => {
      return {
        ...prevState,
        products: value,
      };
    });
    }

    if (name === "type") {
      setClient((prevState) => {
      return {
        ...prevState,
        type: value,
      };
    });
    }

    if (name === "status") {
      setClient((prevState) => {
      return {
        ...prevState,
        status: value,
      };
    });
    }

    validateField(!isEmpty(value.toString()), name);
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setClientError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setClientError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(client.client_name), "client_name");
    const check2 = validateField(!isEmpty(client.client_code), "client_code");
    return check1 && check2;
  }

  function resetFields() {
    setClient({
      client_name: "",
      client_code: "",
      owner_representative_id: 0,
      owner_representative_name: "",
      capturing_representative_id: 0,
      capturing_representative_name: "",
      note: "",
      contact: "",
      products: "",
      type: 0,
      status: 0,
    });
    setClientError({
      client_name: false,
      client_code: false,
      owner_representative_id: false,
      owner_representative_name: false,
      capturing_representative_id: false,
      capturing_representative_name: false,
      note: false,
      contact: false,
      products: false,
      type: false,
      status: false,
    });
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    resetFields();
    setOpen(false);
  };

  const handleClick = () => {
    if (!validateAll()) {
      return;
    }
    onAdd(client);
    resetFields();
    setOpen(false);
  };

  return (
    <div>
      <Can 
          role={userPrivilege} 
          perform="clients:edit"
          data={[]} 
          yes={() => {
            setIsButtonDisabled(false);
            return null
          }}
          no={() => {
            setIsButtonDisabled(true);
            return null
          }}
      />
      <Button
        disabled={isButtonDisabled}
        variant="contained"
        size="large"
        color="primary"
        onClick={handleClickOpen}
        className={classes.button}
      >
        Agregar
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Agregar Cliente</DialogTitle>
        <DialogContent>
          <div className={classes.root}>
            <TextField
              onChange={handleChange}
              value={client.client_name}
              name="client_name"
              label="Nombre"
              style={{ margin: 8 }}
              placeholder=""
              helperText={clientError.name && "Este campo no puede estar vacio"}
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              onChange={handleChange}
              value={client.client_code}
              name="client_code"
              label="RIF"
              style={{ margin: 8 }}
              helperText={clientError.name && "Este campo no puede estar vacio"}
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              select
              error={clientError.owner_representative_name}
              onChange={handleChange}
              value={client.owner_representative_name}
              name="owner_representative_name"
              label="Vendedor asignado"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                clientError.owner_representative_name &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {representatives.map((representative) => (
                <MenuItem
                  key={representative.id}
                  value={representative.fname + " " + representative.lname}
                >
                  {representative.fname + " " + representative.lname}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              select
              error={clientError.capturing_representative_name}
              onChange={handleChange}
              value={client.capturing_representative_name}
              name="capturing_representative_name"
              label="Capturado por"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                clientError.capturing_representative_name &&
                "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            >
              {representatives.map((representative) => (
                <MenuItem
                  key={representative.id}
                  value={representative.fname + " " + representative.lname}
                >
                  {representative.fname + " " + representative.lname}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              multiline
              onChange={handleChange}
              value={client.contact}
              name="contact"
              label="Informacin de contacto"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              multiline
              onChange={handleChange}
              value={client.note}
              name="note"
              label="Nota"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              multiline
              onChange={handleChange}
              value={client.products}
              name="products"
              label="Productos de inters"
              style={{ margin: 8 }}
              placeholder=""
              helperText=""
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              select
              onChange={handleChange}
              value={client.status}
              name="status"
              label="Estado"
              className={classes.textField}
              helperText=""
              margin="normal"
            >
              {statusValues.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              select
              onChange={handleChange}
              value={client.type}
              name="type"
              label="Tipo"
              className={classes.textField}
              helperText=""
              margin="normal"
            >
              {typeValues.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={handleClick} color="primary">
            Agregar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

